  ._container hr,
  ._container a, ._container abbr, ._container acronym, ._container address, ._container big, ._container cite, ._container code,
  ._container del, ._container dfn, ._container em, ._container img, ._container ins, ._container kbd, ._container q, ._container s, ._container samp,
  ._container small, ._container strike, ._container strong, ._container sub, ._container sup, ._container tt, ._container var,
  ._container b, ._container u, ._container i, ._container center,
  ._container dl, ._container dt, ._container dd, ._container ol, ._container ul, ._container li,
  ._container button,
  ._container fieldset, ._container form, ._container label, ._container legend, ._container textarea, ._container input, ._container select,
  ._container table, ._container caption, ._container tbody, ._container tfoot, ._container thead, ._container tr, ._container th, ._container td,
  ._container article, ._container aside, ._container canvas, ._container details, ._container embed,
  ._container figure, ._container figcaption, ._container footer, ._container header, ._container hgroup,
  ._container menu, ._container nav, ._container output, ._container ruby, ._container section, ._container summary,
  ._container time, ._container mark, ._container audio, ._container video {
    vertical-align: baseline;
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    font-smoothing: antialiased; }
  ._container article, ._container aside, ._container details, ._container figcaption, ._container figure,
  ._container footer, ._container header, ._container hgroup, ._container menu, ._container nav, ._container section {
