    text-decoration: none; }
  ._container button,
  ._container input,
  ._container textarea,
  ._container select,
  ._container a {
    background-color: transparent;
    font-family: inherit;
    font-size: inherit;
    line-height: inherit; }
  ._container button,
  ._container a {
    letter-spacing: inherit;
    word-spacing: inherit;
    cursor: pointer; }
